      <div v-if="$global.global_ru">
        <h2><span>Основные проблемы:</span></h2>
        <p>В практике региональных журналистов в основном больше гражданских исков, связанных с иском о защите чести и достоинства, чем уголовных дел. Между тем, некоторые региональные издания отмечают, что в последнее время стало меньше судебных процессов с участием журналистов. </p>

        <p>Как отметили практически во всех редакциях, досудебных претензий поступает много. В основном, конфликты останавливаются на досудебных претензиях. Предусмотренная гражданским законодательством досудебная стадия помогает в урегулировании конфликта, когда прежде чем обратится в суд, необходимо отправить претензию в СМИ. Но некоторые судьи в регионах не знают о существующей процедуре либо игнорируют и принимают иски без обязательной процедуры досудебного регулирования.</p>

        <p>В случае предъявления претензий СМИ обычно используют право на ответ, в редких случаях - опровержение.  </p>

        <p>Журналисты отмечают, что законодательство не позволяет проводить журналистские расследования. В частности, нормы о диффамации, о неприкосновенности частной жизни тормозят развитие этого жанра. </p>

        <p>Претензии к журналистам поступают в основном от представителей государственных органов. </p>

        <p>Многие региональные редакции не могут позволить себе содержать в штате юриста. При этом отмечают, что у государственных СМИ проблем не возникает, досудебные претензии поступают к ним редко. </p>

        <p>Несмотря на установленные законодательством нормы о том, что юридические лица не могут требовать возмещения морального вреда, ПГУ (Павлодарский университет) обратился с иском на 40 млн тенге к «Павлодар онлайн», требуя опровержения новости трехлетней давности. 
        Нередко возникают проблемы со сроками исковой давности. Поскольку закон не устанавливает такие сроки, в Шымкенте идет суд по публикации 2011 года к сетевому изданию titus.kz. Истец считает, что в материале незаконно используется его имя. Но больше всего его возмущают комментарии под материалом, которых за девять лет набралось немало.</p>
        
        <p>Среди уголовных дел можно отметить процессы, связанные с клеветой. Корреспондент «Тобол-Инфо» Алина Сушко была оправдана за отсутствием состава преступления. Поводом послужил материал, обидевший местного капитана полиции. А вот Елену Кузнецову сначала осудили на год ограничения свободы и штраф 36 000 тенге. Позже апелляционная коллегия отменила решение суда первой инстанции, посчитав, что суд не обеспечил всестороннее, полное и объективное исследование обстоятельств дела и что частному обвинителю не удалось представить в суде доказательства
        «распространения не соответствующих действительности сведений».</p>

        <p>Журналисты уверены, что в штате должен быть юрист, который будет просматривать все материалы до выхода в печать. Также должна быть доказательственная база по всем спорным статьям. </p>

        <p>В случае судебных претензий СМИ обычно привлекают независимую экспертизу и обращаются за помощью к адвокатам и медийным организациям.
        Одна из причин, по которой исков стало меньше - журналисты стали более профессиональными и грамотными.</p>

        <p>Казахоязычные журналисты отмечают, что сегодня журналисту приходится балансировать в части сохранения прав на неприкосновенность частной жизни. Всеобщая гласность привела к тому, что иногда золотая середина нарушается во имя рейтингового материала. Грамотность СМИ в этой сфере надо постоянно повышать во избежание спорных моментов</p>
        <div class="highlight">
          <h2><span>Что делать?</span></h2>
          <ul>
              <li>При назначении сумм морального вреда необходимо учитывать принцип соразмерности. Истец должен доказывать нравственные страдания в суде. Штрафы должны быть разумными и соразмерными. </li>
              <li>Установить срок исковой давности по публикациям в СМИ в один год.</li>
              <li>Обязать судей соблюдать процедуру досудебного урегулирования.</li> 
              
              <li>Привести законодательство в соответствии с международными стандартами в области свободы выражения мнения.</li>
              <li>Декриминализировать наказание деяния, связанные с реализацией права на свободу выражения мнений (оскорбление, распространение заведомо ложной информации, возбуждение розни)</li>
              <li>Ввести государственную пошлину, соразмерную предъявляемым суммам исков к СМИ по возмещению морального ущерба.</li>
              <li>Ввести понятие «общественный интерес», «общественное место» и «публичное лицо» в национальное законодательство.</li>
          </ul>
