        <h2><span>Негізгі мәселелер:</span></h2>
        <p>Аймақтық журналистер тәжірибесінде қылмыстық істерге қарағанда, ар-намыс және беделге қатысты азаматтық талап арыз көбірек. Кейбір аймақтық басылым өкілдерінің хабарлауынша, соңғы уақытта журналистердің қатысуымен өтетін қылмыстық істер қатары азайған. </p>

        <p>Барлық дерлік редакция өкілдері айтқандай, сотқа дейінгі шағым өте көп түседі. Негізінен, көптеген дау сотқа дейінгі талаппен шектеледі. Азаматтық заңмен көзделген сотқа дейінгі саты дауды реттеуге көмектеседі, сотқа жүгінбес бұрын талапты БАҚ-қа жолдаған жөн. Алайда, бірқатар судьялар бұл қолданыстағы тәжірибеден хабарсыз болса керек, талап арызды сотқа дейінгі шараларсыз қабылдай береді. </p>

        <p>БАҚ-қа шағым жазылған жағдайда жауап беру құқығы, сирек жағдайда теріске шығару құқығы пайдаланылады. </p>

        <p>Журналистер қазіргі заңнаманың журналистік зерттеулер жүргізуге мүмкіндік бермейтінін айтады. Әсіресе, диффамация туралы, жеке өмірге қол сұғылмаушылыққа қатысты нормалар аталған жанрды тежеп келеді. </p>

        <p>Журналистерге шағым көп жағдайда мемлекеттік органдар өкілдері тарапынан болады. </p>

        <p>Көптеген аймақтық редакцияларда штатта заңгер ұстауға мүмкіндік жоқ. Бұл ретте мемлекеттік БАҚ өкілдерінде мәселе туындамайды, сотқа дейінгі шағым оларға өте сирек түседі. </p>

        <p>Заңды тұлғалардың моральды шығын өндіре алмайтыны заңмен бекітілсе де, ПМУ (Павлодар университеті) «Павлодар онлайнға» үш жыл бұрынғы жаңалықты жоққа шығару жөнінде және 40 млн теңгеге талап арыз берді.</p>

        <p>Талап мерзіміне қатысты мәселелер де жиі туындайды. Заң оған шектеу қоймағандықтан, Шымкентте, мысалы, 2011 жылғы материал бойынша titus.kz сайтына қатысты сот жүріп жатыр. Талапкердің пайымынша, материалда оның есімі заңсыз пайдаланылған. Бәрінен бұрын материал астына тоғыз жыл бойы жазылған пікірлер көңілінен шықпаса керек. </p>
        
        <p>Қылмыстық істер арасында жала жабуға қатысты процестерді атап өтуге болады. «Тобол-Инфо» тілшісі Алина Сушко қылмыс құрамы болмауына байланысты ақталып шықты. Материал жергілікті полиция капитанын ренжіткенге ұқсайды. Ал, Елена Кузнецованы сот алдымен бір жылға бас бостандығынан шектеп, 36 000 теңге айыппұл салды. Кейін апелляциялық саты бірінші инстанция сотының шешімінің күшін жойды, соттың пайымынша, жағдайлар жан-жақты, толық және объективті зерттелмеген, жеке айыптаушы «шындыққа сай келмейтін мәліметтердің таралғаны» жөнінде дәлелдемелер көрсете алмаған. </p>

        <p>Журналистер штатта жарияланбас бұрын барлық материалды қарап шығатын заңгер болу керектігін алға тартады. Сонымен бірге, барлық даулы материал бойында дәлелдемелер базасы болған жөн деседі. </p>

        <p>Әдетте, БАҚ-қа сотқа дейінгі шағым түскен жағдайда тәуелсіз сараптама жұмылдырылады және адвокаттар мен медиа ұйымдарға жолығады. </p>

        <p>Талап арыз санының азаюына, сонымен бірге, журналистердің кәсіби тұрғыда сауатты болуы да себеп. </p>
        <p>Қазақтілді журналистер жеке өмірге қол сұғылмаушылық құқығы тұрғысынан тепе-теңдік сақтау керектігін атап өтті. Жалпы ашықтық кейде материал рейтингіне жұмыс істеп, тепе-теңдік бұзылады деп санайды. Даулы жайттарды болдырмау мақсатында осы салада БАҚ сауаттылығын жоғарылату керек.</p>

