              <li>Пікір білдіру бостандығы құқығын жүзеге асыруға бағытталған әрекетке жазаны қылмыстық заңнан алып тастау (қорлау, көрінеу жалған ақпарат тарату, араздық қоздыру)</li>
              <li>Моральды шығынды өтеу жөнінде БАҚ-қа қойылған талап сомасына мөлшерлес мемлекеттік баж енгізу керек</li>
